.primary-btn{
    position: relative;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 12px 34px;
    text-align: center;
    height: 36px;

    font-family: inherit;
    font-weight: 600;
    font-size: 14px;
    line-height:1.29;
    
    color: var(--primary-text-color);
    background-color: var(--accent);
    border: none;
    border-radius: 10px;
    overflow: hidden;
    transition: color $animation-time $animation-cubic, visibility $animation-time, background-color $animation-time $animation-cubic, visibility $animation-time;
    
    &:hover{
        background-color: var(--primary-color-btn);
    }
    &:focus{
        color: var(--light-color);
    }
    &:disabled{
        color: var(--primary-text-color);
        background-color: var(--disabled-btn);
    }
}

